  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
  font-family: "Roboto", sans-serif;
}

body {
  font-size: 1.6rem;
  overflow-y: hidden;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
