}

input {
  outline: none;
  border: none;
  background-color: transparent;
  font-size: 1.6rem;
}

.content {
  width: 100%;
  height: 100vh;
  background-color: #f6f6f6;
  display: grid;
  grid-template-columns: 285px 1fr;
}

#waveImage {
